import wx from 'weixin-jsapi';
import { isPrimitive } from 'util';

export default {
    data () {
        return {
            form: {
                title: '',
                description:'',
                serialID:'',
                priority:'',
                date:''
            },
            Submit: 'Submit',
            isSubmit: false,
            SubjectValidate: [
                { validate: (val) => !!val, message: 'Please Input Subject' }
            ],
            DescriptionValidate: [
                { validate: (val) => !!val, message: 'Please Input Description' }
            ],
            codeOptions: [
                {code:"1",text:"Immediate"},
                {code:"2",text:"Urgent"},
                {code:"3",text:"Normal"},
                {code:"7",text:"Low"}
            ],
        }
    },
    computed: {
        wxCode () {
            return this.$route.query.code;
        }
    },
    methods: {
        submitTicket: function() {
            let thiz = this;
            let isPrd =  Object.is(process.env.NODE_ENV, 'production');
            this.$refs.form.validate().then((result) => {
                if (!result) {
                    return false
                } else {
                    thiz.$confirm('Confirm your information?', 'Confirm').then(({ result }) => {
                        if (result) {
                            var loading = this.$loading();
                            this.WXUserInfo.then((userInfo) => {
                                this.$axios({
                                    url: this.CONFIG.url.createTicket,
                                    data: {
                                        openID: userInfo.openid,
                                        nickName: userInfo.nickname,
                                        Name: this.form.title,
                                        Description: this.form.description,
                                        SerialID: this.form.serialID,
                                        ServicePriorityCode: this.form.priority,
                                        RequestedFulfillmentPeriodStartDateTime: this.form.date
                                    },
                                    method: 'post'
                                }).then((res) => {
                                    loading.close();
                                    if(res.status == 200) {
                                        // thiz.Submit = 'Submit';
                                        thiz.isSubmit = false;
                                        thiz.$toast.success('Create Successfully');
                                        if (isPrd) {
                                            wx.ready(() => {
                                                wx.closeWindow();
                                            })
                                        }
                                    }
                                }).catch((err) => {
                                    loading.close();
                                    thiz.$alert('Please bind Contact first!');
                                    // if (isPrd) {
                                    //     wx.ready(() => {
                                    //         wx.closeWindow();
                                    //     })
                                    // }
                                })
                            })
                        }
                    })
                }
            });
        },
        cancelInput: function () {
            this.form = {
                title: '',
                description:'',
                serialID:'',
                priority:'',
                date:''
            }
        },
        ChangeSelect: function () {}
    },
    mounted: function () {
        this.$axios({
            method: 'post',
            url: this.CONFIG.url.wxConfig,
            data: {
                url: location.href.split('#')[0]
            }
        }).then((data) => {
            console.log(data.data);
            wx.config({
                debug: false,
                appId: data.data.appId,
                timestamp: data.data.timestamp,
                nonceStr: data.data.nonceStr,
                signature: data.data.signature,
                jsApiList: ['closeWindow']
            })
        });
        this.WXUserInfo = this.common.getWXUserInfo(this.wxCode);
    }
}




